div::after{
  content: "";
  position: absolute;
  top: 100px;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 70%;
  transform: scale(0.75);
  filter: blur(50px);
  background: linear-gradient(270deg, #D03130, #7e0fff);
  background-size: 200% 200%;
  animation: glowing 10s ease infinite;
}

@keyframes glowing {
  0%{
    background-size: 0% 50%;
  }
  50%{
    background-size: 100% 50%;
  }
  100%{
    background-size: 0% 50%;
  }
}

@keyframes animate{
  0%{
    color: #7e0fff;
  }
  50%{
    color:#0fffc1;
  }
  100%{
    color:#7e0fff;
  }
}
